.game-rules {
    width: 100%;
    min-height: 100vh;
    position: relative;

    & .grid-wrapper {
        height: 100%;
        position: absolute;
        grid-template-rows: 1fr 0fr 1fr;
    }

    &__image {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__text-board {
        grid-column: 1 / -1;
        justify-self: center;
        align-self: flex-end;
        margin-top: 22vh;
        margin-bottom: 60px;
    }

    &__text {
        grid-column: 1 / -1;
        text-align: center;
        font-size: 22px !important;
    }

    &__buttons {
        display: flex;
        grid-column: 1 / -1;
        align-self: flex-start;
        margin-top: 40px;
        justify-self: center;

        & > button {
            margin: 0 20px;
            padding-top: 17px;
            padding-bottom: 17px;
            width: 260px;
        }
    }
}
