.game_rules_container {
  border-radius: 10px 10px 0px 0px;
  height: 759px;
  overflow: scroll;
  position: relative;
  width: 706px;
  top: -3073px;
  left: 8520px;

  & .game_rules_img {
    height: 1751px;
    position: absolute;
    width: 1313px;
    left: -303px;
    top: -496px;
  }

  & img {
    height: 1751px;
    position: absolute;
    width: 1313px;
    left: -303px;
    top: -496px;
  }

  &.game_rules_header_frame {
    display: flex;
    height: 42px;
    left: 263px;
    line-height: 42px;
    position: absolute;
    top: 40px;
    white-space: nowrap;
    width: 172px;
  }

  & .header {
    color: #ffffff;
    display: flex;
    font-family: Poppins, 'Source Sans Pro';
    font-size: 28px;
    font-weight: 700;
    height: 42px;
    left: 263px;
    line-height: 42px;
    position: absolute;
    top: 40px;
    white-space: nowrap;
    width: 172px;
    align-items: center;
    text-align: justify;
  }

  & .game_rules_body {
    box-sizing: border-box;
    height: 675px;
    left: 34px;
    overflow: scroll;
    padding: 10px 10px;
    position: absolute;
    top: 88px;
    width: 638px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  & .body {
    color: #ffffff;
    display: flex;
    font-family: Poppins, 'Source Sans Pro';
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    max-width: 619px;
    height: 840px;
    align-items: center;
    text-align: justify;
    flex: none;
    order: 0;
    flex-grow: 0;
  }
}