.button {
    all: unset;
    font-size: 16px;
    color: #fff;
    padding: 13px 20px;
    background-color: #000000;
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    box-sizing: border-box;

    &--secondary {
        background-color: #fff;
        color: #000;
        border-color: #fff;
    }

    img {
        margin-right: 10px;
    }
}
