.why-should-join {
    width: 100%;
    min-height: 100vh;
    position: relative;
    padding-bottom: 150px;

    & .grid-wrapper {
        height: 100%;
        position: relative;
        margin: 0 100px;
    }

    &__image {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__text-board {
        grid-column: 1 / -1;
        justify-self: center;
        align-self: flex-end;
        margin-top: 10vh;
        margin-bottom: 150px;
    }

    &__slider {
        grid-column: 1 / -1;

        .slick-list {
            overflow: visible;

            .slick-track {
                display: grid;
                grid-template-columns: repeat(12, 1fr);

                .slick-slide {
                    transition: transform 0.4s;
                    padding: 0 10px;
                    box-sizing: border-box;
                    transform: scale3d(1, 0.9, 1);
                    z-index: 1;

                    &,
                    & > div {
                        height: 100%;
                    }

                    &.slick-active {
                        transform: scale(1);
                        z-index: 2;
                    }

                    &.slick-current + .slick-slide {
                        transform: scale3d(1.2, 1.1, 1);
                        z-index: 3;
                    }
                }
            }
        }

        &__item {
            border-radius: 11px;
            height: calc(100% - 6px);
            padding: 20px;
            margin: 3px;
            background: rgb(218, 177, 220);
            box-sizing: border-box;
            background: linear-gradient(
                139deg,
                rgb(218, 177, 220) -15%,
                rgb(255, 255, 255) 20%,
                rgb(251, 247, 251) 78%,
                rgb(218, 177, 220) 107%
            );

            &-border-wrap {
                height: 100%;
                background: rgb(218, 177, 220);
                border-radius: 11px;
                overflow: hidden;
                background: linear-gradient(
                    131deg,
                    rgb(248 80 255) 0%,
                    rgb(254 239 255) 27%,
                    rgb(254 239 255) 81%,
                    rgb(248 80 255) 100%
                );
            }

            img {
                width: 100%;
                height: 300px;
                object-fit: cover;
                border-radius: 11px;
            }

            &__title {
                color: #451660;
                margin-top: 70px;
                text-align: center;
                font-size: 25px;
                font-weight: bold;
            }

            &__text {
                text-align: center;
                font-size: 15px;
                margin-top: 50px;
            }
        }
    }
}
