.back-button {
  background-color: rgba(255, 244, 193, 1);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
  display: flex;
  font-size: 22px;
  font-weight: 700;
  height: 38px;
  justify-content: center;
  left: 20px;
  line-height: 1.5;
  position: absolute;
  top: 77px;
  white-space: nowrap;
  width: 104px;

  & img {
    max-height: 50px;
    margin-top: 0px;
  }

  & span {
    display: flex;
    width: 100%;
    //background-color: #fff;
    color: #ffffff;
    font-weight: 700;
    height: 33px;
    align-items: center;
    justify-content: center;
    font-size: 22px;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    text-transform: uppercase;
    position: absolute;
    bottom: 0;
  }
}