html {
    overflow-x: hidden;
}

body {
    margin: 0;
    font-family: 'Poppins', 'Segoe UI', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.grid-wrapper {
    display: grid;
    grid-template-columns: repeat(12, minmax(0, 1fr));
    column-gap: 32px;
    margin: 0 220px;
    position: relative;
}

.text {
    font-size: 30px;
    font-weight: bold;
    color: #fff;
    text-shadow: 2px 0 #000, -2px 0 #000, 0 2px #000, 0 -2px #000, 1px 1px #000,
        -1px -1px #000, 1px -1px #000, -1px 1px #000;
}

@font-face {
    font-family: 'BenguiatStd';
    src: local('BenguiatStd'),
        url('./assets/fonts/BenguiatStd-Bold.otf') format('opentype');
    font-weight: bold;
}
