.menu {
    position: fixed;
    z-index: 20;
    padding: 20px 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    box-sizing: border-box;
    backdrop-filter: blur(10px);
    background-color: #ffffff21;

    &-right {
        display: flex;
        margin-right: 5px;
        align-items: center;

        > * {
            margin: 0 15px;
        }
    }

    &__items {
        &__item {
            color: #000;
            text-decoration: none;
            margin: 0 20px;
            font-weight: bold;
            transition: all 0.2s;

            &:hover {
                color: #fff;
                text-shadow: 2px 0 #000, -2px 0 #000, 0 2px #000, 0 -2px #000,
                    1px 1px #000, -1px -1px #000, 1px -1px #000, -1px 1px #000;
            }
        }
    }
}
