.wallet-button {
    background-color: rgb(255, 255, 255);
    border: none;
    box-shadow: 0px 2px 2px rgba(133, 37, 4, 0.25);
    border-radius: 12px;
    width: 190px;
    height: 186px;
    justify-content: center;
    align-items: center;
    padding: 0px;
    margin: 10px;
    position: relative;
    cursor: pointer;

    & img {
        max-height: 70px;
        margin-top: -40px;
    }

    & span {
        display: flex;
        width: 100%;
        background-color: #ffcb14;
        color: #000000;
        font-weight: 600;
        height: 60px;
        align-items: center;
        justify-content: center;
        font-size: 22px;
        border-bottom-left-radius: 12px;
        border-bottom-right-radius: 12px;
        text-transform: uppercase;
        position: absolute;
        bottom: 0;
    }
}