.about {
    width: 100%;
    min-height: 100vh;
    position: relative;

    & .grid-wrapper {
        height: 100%;
        position: absolute;
    }

    &__image {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__text-board {
        grid-column: 1 / -1;
        justify-self: center;
        align-self: flex-end;
        margin-bottom: 150px;
    }

    &__text {
        grid-column: 1 / -1;
        text-align: center;
        font-size: 22px !important;
    }
}
