.banner {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    background-color: #81c2c5;

    &__image {
        object-fit: cover;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        background-color: #fff;
    }

    &__clouds {
        width: 100vw;
        height: 100vh;
        overflow: hidden;
        position: absolute;

        img {
            width: 85vw;
            position: absolute;
        }
    }

    &__content {
        position: relative;
        justify-items: center;
        align-items: center;
        height: 100%;
        grid-template-rows: 1fr 0fr 0fr;

        &__text-board {
            grid-column: 1 / -1;
        }

        &__buttons {
            display: flex;
            grid-column: 1 / -1;

            & > button {
                margin: 0 20px;
                padding-top: 17px;
                padding-bottom: 17px;
                width: 200px;
            }
        }

        &__text {
            grid-column: 1 / -1;
            margin-top: 70px;
            margin-bottom: 100px;
            text-align: center;
        }
    }
}
