.connect_wallet_container {
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow-x: hidden;
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: #008000;
    background-image: url('../../assets/images/wallet_background.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: #ffffff;
    // padding: 80px 100px 100px 100px;

    & .header {
        color: #ffffff;
        font-family: Poppins, 'Source Sans Pro';
        font-size: 50px;
        font-weight: 700;
        margin-bottom: 30px;
    }

    & .sub_header {
        color: #ffffff;
        flex-shrink: 0;
        font-family: Poppins, 'Source Sans Pro';
        font-size: 40px;
        font-weight: 600;
        margin-bottom: 60px;
        text-align: center;
        max-width: 80%;
        // text-justify: ;
    }

    & .wallet_button_group {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 60px;
        max-width: 90%;
    }

    .link_container {
        & span {
            font-size: 34px;
            font-weight: 700;

            & a {
                color: #ffcb14;
                text-transform: uppercase;
                text-decoration: none;
            }
        }
    }
}